<template>
    <div class="page page-contact">
        <div class="pagewrap">
            <div class="contact">
                <p class="fade-me">I'm Daniel, and I love making cool websites.</p>
                <p class="fade-me">Want to build something together?</p>
                <p class="fade-me">View my
                    <a class="cred text-link" href="https://tealicious.github.io/" target="_blank">credentials</a>, and get @ me:</p>
                <div class="link-section-wrap">
                    <div class="text-links">
                        <a class="email fade-me" href="mailto:carterdanielr@gmail.com" target="_blank">carterdanielr@gmail.com</a>
                        <a class="phone fade-me" href="tel:513-519-7464">513.519.7464</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Github from "../components/svg/Github.vue";
const increment = 0.1;
export default {
  components: {
    appGithub: Github
  },
  mounted() {
    let fadeIns = this.$el.querySelectorAll(".fade-me");
    let animationDelay = 0;
    for (let i = 0; i < fadeIns.length; i++) {
      fadeIns[i].style.animationDelay = animationDelay + "s";
      animationDelay += increment;
    }
  }
};
</script>
<style lang="scss" scoped>
.text-links {
  display: flex;
  flex-direction: column;
  a {
    align-self: flex-start;
  }
}
</style>


